<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form v-on:submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="employee_id" class="validate" required>
            <label for="">Employee ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" class="validate" required>
            <label for="">Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" class="validate" required>
            <label for="">Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" class="validate" required>
            <label for="">Position</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit';

export default {
  name: 'edit-employee',
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('employees')
      .where('employee_id', '==', to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.name = doc.data().name;
            vm.dept = doc.data().dept;
            vm.position = doc.data().position;
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('employees')
      .where('employee_id', '==', this.$route.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
            M.updateTextFields();
        })
      })
    },
    updateEmployee() {
      db.collection('employees')
      .where('employee_id', '==', this.$route.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            employee_id: this.employee_id,
            name: this.name,
            dept: this.dept,
            position: this.position
          })
          .then(() => {
            this.$router.push({
              name: 'view-employee',
              params: {
                employee_id: this.employee_id
              }
            })
          });
        });
      });
    }
  },
  updated() {
    M.updateTextFields();
  }
}
</script>

<style>

</style>



